html, body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root {
  position: relative;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container {
  display: grid;
  grid-gap: 0;
  grid-template-columns: repeat(7, 1fr);
  grid-template-rows: -webkit-min-content 1fr;
  grid-template-rows: min-content 1fr;
}

.input-container {
  padding-top: 6.5vmin;
  grid-column: 1/3;
  grid-row: 2/3;
  grid-gap: 0;
  height: 100vh;
}

.results-container {
  padding-top: 6.5vmin;
  grid-column: 3/8;
  grid-row: 2/3;
  margin: 0;
  height: 100vh;
  overflow: hidden;
}

.home-logo {
  height: 10vmin;
}

.home-header {
}

.home-link {
  color: #61dafb;
}

.title {
  color: red;
}

.search {
    padding-left: 0.3em;
    padding-right: 0.3em;
    padding-top: 0;
    margin: 0;
    grid-column: 1/2;
    grid-row: 1/2;
}

.gene-list {
    width: 100%;
}

.buttons {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    margin-bottom: 0.4em;

}

.gene-list-wrapper {
  height: 100%;
  padding: 0.5em;
  overflow: auto;
}

.gene-list {
  overflow: auto;
}

.loading-container {
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.1);
    color: #FFFFFF;

    display: flex;
    align-items: center;
    justify-content: center;
}
.input-wrapper {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.empty {
  background: #F5F5F5;
}

.message {
  background: #F5F5F5;
  height: 100vh;
  width: 100%;
  display: inline-flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.home-logo {
  height: 15em;
}

.results-wrapper {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.ndex-base {
  background-color: #ffffff;
  display: flex;

  height: 100vmin;
  position: relative;

}

.network-view {
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
}

.property-view {
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
}

.network-list {
  height: 100%;
  overflow: auto;
}

.gutter {
  margin: 0;
  padding: 0;
  background-color: rgba(230, 230, 230, 0.5);
  background-repeat: no-repeat;
}

.drawerHeader {
  display: flex;
  padding: 0 8px;
  align-items: center;
  justify-content: flex-end;
}

